import React from 'react';
import {NavLink} from 'react-router-dom';
import vector from '../Images/Vector.png';

const Navbar = ()=>{

  return (
    <>
      <div className='container-fluid'>
        <nav className="navbar navbar-expand-lg navbar-light mx-5 px-5">
            <div className="container-fluid">
              <NavLink className="navbar-brand" to="/"><h1><img src={vector}/> Awais</h1></NavLink>
              <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
              </button>
              <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                  <li className="nav-item">
                    <NavLink className="nav-link" to="/">CATALOGUE</NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink className="nav-link" to="/fashion">FASHION</NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink className="nav-link" to="/favorite">FAVOURITE</NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink className="nav-link" to="/lifestyle">LIFESTYLE</NavLink>
                  </li>
                </ul>
                <form className="d-flex">
                  <button className="btn btn-dark" type="submit">Sign Up</button>
                </form>
              </div>
            </div>
          </nav>
      </div>

    </>
  )
}

export default Navbar;